Mock.mock(/^\/api\/blogs(\?.+)?$/, "get", function (options) {
  const query = qs.parse(options.url);
  function getQueryString (name) {
    var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)", "i");
    var r = window.location.search.substr(1).match(reg);
    if (r != null) return unescape(r[2]);
    return null;
  }
  const categoryid = query.categoryid;
  const articleCount = parseInt(query.articleCount);
  const sum = query.limit * getQueryString("page");
  let consult = null;
  console.log(sum);
  if (articleCount < sum) {
    consult = 10 - sum % articleCount;
    console.log(consult);
  } else {
    consult = 10;
  }
  console.log(consult);
  return Mock.mock({
    code: 0,
    msg: "",
    data: {
      [`total|${articleCount}`]: 1,
      [`rows|${consult}`]: [
        {
          id: "@guid",
          title: "@ctitle",
          description: "@cparagraph(1,10)",
          category: {
            [`id|${categoryid}`]: -1,
            name: "@id"
          },
          "scanNumber|0-3000": 0,
          "commentNumber|0-300": 30,
          "thumb|1": ["@image(300x250, @color, #fff, @natural)"],
          createDate: `@date('T')`
        }
      ],
    },
  })
});

